import styled from 'styled-components';

export const Container = styled.a.attrs({
	target: "blank",
	style:{
		textDecoration: 'none'
	}
})`
	width: 358px;
	height: 338px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;

	padding: 30px 30px;
	margin: 20px;

	border: 2px solid #008ad8;
	border-radius: 8px;

	box-shadow: 0 0 20px black, inset 0 0 10px #008ad8;

	transition: transform;

	:hover{
		background-color: rgba(0,138,216,0.1);
		box-shadow: 0 0 20px #008ad8,  0 0 30px black, inset 0 0 30px #008ad8;

		transform: scale(1.02);
	}
	
	#header{
		width: 100%;
		display: flex;
		flex-direction: row;

		align-items: center;
		
		img{
			height: 80px;
			margin-right: 14px;
		}

		div{
			h1{
				color: #008ad8;
				font: 700 14px 'Dm Sans';
			}

			h2{
				color: #fff;
				font: 400 22px 'Rufina';
			}
		}
	}

	#description{
		margin-top: 34px;

		p{
			color: #ABAEB0;
			font: 400 18px 'Dm Sans';
		}
	}
`;