import styled from 'styled-components';

interface MenuProps{
	menuEnabled: boolean;
}

interface ContentProps{
	onClick: () => void;
}

export const Container = styled.div`
	z-index: 0;
	display: grid;

	a.anchor {
	display: block;
	position: relative;
	top: -92px;
	visibility: hidden;
	}

	scroll-behavior: smooth;
	background-color: #252829;
`;

export const Header = styled.div`
	z-index: 2;

	width: 100%;
	height: 93px;

	position: fixed;

	grid-row-start: 1;
	grid-row-end: 2;

	display: flex;
	flex-direction: row;

	align-items: center;
	justify-content: space-between;

	top: 0;
	padding: 0 25px;

	background-color: #313536;
	/* border-bottom: 2px solid #252829; */
	box-shadow:0 0 30px 2px black;
	
	#logo{
		img {
			height: 50px;
			max-width: 90%;
		}
	}
	
	button{
		height: 30px;
		width: 30px;
		
		img{
			height: 22px;
			width: 22px;
		}
	}
	
	#options{
		margin-top: -5px;
		display: flex;
		gap: 12px;
		align-items: flex-end;

		a{
			color: #ABAEB0;
			font: 400 15px 'Dm Sans';
			text-shadow: 0 0 20px black;

			display: flex;
			flex-direction: column;
			justify-content: space-between;
			align-items: center;

			img{
				width: 18px;

			}

			:hover{
				color: #008ad8;
				text-shadow: 0 0 20px black, 0 0 30px #008ad8;
			}
		}
		
		#contact{
			color: #fff;
		}
	}
	`;

export const Content = styled.div<ContentProps>`
	width: 100%;

	grid-row-start: 2;
	grid-row-end: 3;

	display: flex;
	flex-direction: column;

	align-items: center;
	justify-content: space-between;

	padding-top: 90px;
`;

export const Menu = styled.ul<MenuProps>`
	z-index: 1;
	position: fixed;
	right: 0;
	margin-top: 1px;

	height: 100%;

	display: flex;
	flex-direction: column;
	gap: 40px;
	
	align-items: flex-start;
	justify-content: start;

	padding: 24px;

	background-color: #313536;

	box-shadow: 0 0px 30px 2px black;
		a {
			color: #ABAEB0;
			font: 400 18px 'Dm Sans';
			text-shadow: 0 0 20px black;

			display: flex;
			align-items: center;
			justify-content: center;
			img{
				height: 18px;
				margin-right: 18px;
			}
			:hover{
				color: #008ad8;
				text-shadow: 0 0 20px black, 0 0 30px #008ad8;
			}
		}
`;

export const About = styled.div`
	width: 100%;

	display: flex;
	flex-wrap: wrap;
	gap: 50px;

	align-items: center;
	justify-content: center;

	padding: 69px 0;

	background-color: #252829;

	img {
		height: 476px;
	}

	#about-div{
		min-width: 380px;

		display: flex;
		flex-direction: column;

		#about-me{
			color: #008ad8;
			font: 700 20px 'Dm Sans';

			margin-bottom: 29px;

		}

		#web-developer{
			color: #fff;
			font: 400 45px 'Rufina';

			margin-bottom: 29px;
		}

		#about-text{
			color: #ABAEB0;
			font: 400 22px 'Dm Sans';
		}
	}
`;

export const Skills = styled.div`
	width: 100%;

	display: flex;
	flex: wrap;
	gap: 50px;
	
	align-items: center;
	justify-content: center;

	padding: 69px 0;

	background-color: #313536;

	@media only screen and (max-width: 1000px){
		flex-direction: column;
	}

	#about-skills{
		transition: all 800ms ease-in;

		#skills-title1{
			color: #008ad8;
			font: 700 20px 'Dm Sans';

			margin-bottom: 29px;
		}

		#skills-title2{
			color: #fff;
			font: 400 40px 'Rufina';
		}

		#skills-text{
			color: #ABAEB0;
			font: 400 18px 'Dm Sans';

			margin-top: 60px;
		}
	}

	#skills-icons{
		min-width: 400px;
		max-width: 500px;

		display: flex;
		flex-wrap: wrap;
		gap: 20px;

		justify-content: center;
	}
`;

export const Projects = styled.div`
	width: 100%;

	display: flex;
	flex-direction: column;

	align-items: center;
	justify-content: center;

	background-color: #252829;

	padding: 88px 0;

	p{
		color: #008ad8;
		font: 700 20px 'Dm Sans';

		margin-bottom: 29px;
	}
	
	#projects-list{
		width: 100%;

		display: flex;
		flex-wrap: wrap;

		align-items: center;
		justify-content: center;
	}
`;

export const Footer = styled.div`
	width: 100%;
	height: 100%;

	display: flex;
	flex-direction: column;

	align-items: center;
	justify-content: center;

	background-color: #313536;

	padding: 88px 0;

	#title{
		color: #008ad8;
		font: 700 20px 'Dm Sans';

		margin-bottom: 60px;
	}
`;