import { Container } from "./styles";

interface Props{
	src: string;
	url?: string;
	name: string;
	download?: boolean;
}

export function Contact({
	src,
	url,
	name,
	download
}:Props){
	return(
		<Container>
			<a href={url} target="_blank" download={download}>
				<p>
					<img src={src} alt={name}/>
					{name}
				</p>
			</a>
		</Container>
	)
}