import { Container } from "./styles";

interface Props{
	src: string;
	category: string;
	title: string;
	description: string[];
	href?: string;
}

export function Project({
	src,
	category,
	title,
	description,
	href
}:Props){
	return(
		<Container href={href}>
			<div id='header'>
				<img src={src} alt={title}/>

				<div>
					<h1>{category}</h1>

					<h2>{title}</h2>
				</div>
			</div>

			<div id='description'>
				{ description.map(item => 
					<p>{item}</p>
				) }
			</div>
		</Container>
	)
}