import { useState } from 'react';
import {useHistory} from 'react-router-dom';

import { 
	About, 
	Container, 
	Content, 
	Footer, 
	Header, 
	Menu, 
	Projects, 
	Skills 
} from './styles';

import { useWindowDimensions } from '../../hooks/useWindowDimensions';

import { Skill } from '../../components/Skill';
import { Contact } from '../../components/Contact';
import { Project } from '../../components/Project';

import logo from '../../assets/logo.svg';
import menu from '../../assets/menu.svg';
import x from '../../assets/x.svg';

import photo from '../../assets/photo.jpg';

import myGPSTracking from '../../assets/myGPSTracking.png'; 
import gofinances from '../../assets/gofinances.svg'; 

import javascript from	'../../assets/javascript.svg';
import reactNative from	'../../assets/react-native.svg';
import sass from	'../../assets/sass.svg';
import typescript from	'../../assets/typescript.svg';
import styled from	'../../assets/styled.svg';
import html from	'../../assets/html.svg';
import css from	'../../assets/css.svg';
import vsCode from	'../../assets/vsCode.svg';
import figma from	'../../assets/figma.svg';
import expo from	'../../assets/expo.png';
import git from	'../../assets/git.svg';
import github from	'../../assets/github.svg';

import email from	'../../assets/email.svg';
import linkedin from	'../../assets/linkedin.svg';
import telefone from	'../../assets/telefone.svg';
import githubBlue from '../../assets/githubBlue.svg';
import curriculo from '../../assets/curriculo.svg';

import person from '../../assets/person.svg';
import message from '../../assets/message.svg';
import project from '../../assets/project.svg';
import lampada from '../../assets/lampada.svg';

import meuCurriculo from '../../assets/Fernando Heusi - Desenvolvedor Front End.pdf'
import { AnimatedInView } from '../../components/LazyAnimation';

export function Home(){
	const [menuEnabled, setMenuEnabled] = useState(false);
	const window = useWindowDimensions();

	function handleButton(){
		setMenuEnabled(state => !state);
	}

	return(
		<Container>
			<Header>
				<a id='logo' href='#about'><img src={logo} alt="Fernando Logo"/></a>

				{window.width>600?
					<div id='options'>
						<a href="#skills" >Habilidades</a>

						<a href="#projects" >Projetos</a>

						<a href="#footer" id='contact'>Contato</a>
					</div>
					:
					<button onClick={handleButton}>
						{menuEnabled? <img src={x}/> : <img  src={menu}/>}
					</button>
				}
			</Header>

			<Content onClick={() => setMenuEnabled(false)}>
				{menuEnabled &&
				<Menu menuEnabled={menuEnabled} onMouseLeave={() => setMenuEnabled(false)}>
						<button onClick={handleButton}><li><a href="#about" ><img src={person}/>Sobre Mim</a></li></button>

						<button onClick={handleButton}><li><a href="#skills" ><img src={lampada}/>Habilidades</a></li></button>

						<button onClick={handleButton}><li><a href="#projects" ><img src={project}/>Projetos</a></li></button>

						<button onClick={handleButton}><li><a href="#footer" id='contact'><img src={message}/>Contato</a></li></button>
				</Menu>
				}

				<a className='anchor' id='about'/>
				<About>
					<img src={photo} alt="Fernando foto"/>

					<div id='about-div'>
						<p id='about-me'>SOBRE MIM</p>

						<p id='web-developer'>Desenvolvedor<br/>Front End</p>

						<p id='about-text'>Sou um entusiasta da tecnologia<br/>
							trilhando meu caminho no<br/>
							desenvolvimento web e mobile.<br/>
						</p>
					</div>
				</About>

				<a className='anchor' id="skills"></a>
				<Skills>
					<div id='about-skills'>
						<p id='skills-title1'>HABILIDADES E FERRAMENTAS</p>

						<p id='skills-title2'>Sempre evoluido,<br/>
							sempre aprendendo</p>

						<p id="skills-text">De todas as habilidades que eu aperfeiçoei,<br/>
						a agilidade em aprender novas tecnologias<br/>
						é a que eu mais tenho orgulho.</p>
					</div>

					<div id='skills-icons'>
						<Skill src={reactNative} name={['react','react-native']}/>
						<Skill src={expo} name={["Expo"]}/>
						<Skill src={javascript} name={['javascript']}/>
						<Skill src={typescript} name={["typescript"]}/>
						<Skill src={sass} name={["sass"]}/>
						<Skill src={styled} name={["styled components"]}/>
						<Skill src={html} name={["html"]}/>
						<Skill src={css} name={["css"]}/>
						<Skill src={vsCode} name={["Vs Code"]}/>
						<Skill src={figma} name={["Figma"]}/>
						<Skill src={git} name={["Git"]}/>
						<Skill src={github} name={["Github"]}/>
					</div>
				</Skills>

				<a className='anchor' id="projects"></a>
				<Projects>
					<p>MEUS PROJETOS</p>

					<div id='projects-list'>
						<Project src={myGPSTracking} category='MOBILE APP' title='MyGPS-Tracking' href='https://github.com/fernandoheusi/MyGPS-Tracking' description={['Aplicativo de aquisição de dados de geolocalização e comunicação com Api. Em situações em que a conexão não é estabelecida (sombra), o aplicativo armazena os dados até que eles possam ser enviados.']}/>

						<Project src={gofinances} category='MOBILE APP' title='Gofinances' href='https://github.com/fernandoheusi/gofinances' description={['Aplicativo para controle financeiro. O login é realizado com conta google ou apple, e as despesas são apresentadas em forma de gráficos']}/>
					</div>
				</Projects>

				<a className='anchor' id="footer"></a>
				<Footer>
					<p id='title'>ENTRE EM CONTATO</p>
					
					<div>
						<Contact name='Meu Currículo' src={curriculo} url={meuCurriculo} download/>

						<Contact name='linkedin.com/in/fernandoheusi/' src={linkedin} url='https://www.linkedin.com/in/fernandoheusi/'/>

						<Contact name='fernandoj.mhs@gmail.com' src={email} url='mailto:fernandoj.mhs@gmail.com'/>

						<Contact name='(21)99630-0109' src={telefone} url='tel:021996300109'/>

						<Contact name='github.com/fernandoheusi' src={githubBlue} url='https://github.com/fernandoheusi'/>
					</div>

				</Footer>
			</Content>
		</Container> 
	)
}